import React, { useState } from "react";
import { Button } from "@nextui-org/react";
import RegistroForm from './RegistroForm';

export default function GestionPacientes({ 
  ninos, 
  tutores, 
  vacunas, 
  lotesVacunas, 
  dosisAplicadas, 
  onUpdateNino, 
  onUpdateTutor,
  onNinoAdd,
  onTutorAdd,
  centros
}) {
  const [searchTerm, setSearchTerm] = useState("");
  const [filterType, setFilterType] = useState("nombre");
  const [searchResults, setSearchResults] = useState([]);
  const [expandedNinoId, setExpandedNinoId] = useState(null);
  const [editingNino, setEditingNino] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleSearch = (e) => {
    const term = e.target.value.toLowerCase();
    setSearchTerm(term);

    if (term.trim() === "") {
      setSearchResults([]);
      return;
    }

    const filteredNinos = ninos.filter((nino) => {
      switch (filterType) {
        case "sector":
        case "provincia":
          return nino.direccion_residencia.toLowerCase().includes(term);
        case "director": // Filtrar por nombre del tutor
          const tutor = tutores.find((t) => t.id_nio === nino.id_nio);
          return tutor && tutor.nombre.toLowerCase().includes(term);
        case "nombre":
        default:
          return nino.nombre_completo.toLowerCase().includes(term);
      }
    });

    const ninosConTutores = filteredNinos.map((nino) => {
      const tutor = tutores.find((t) => t.id_nio === nino.id_nio);
      return {
        ...nino,
        nombre_tutor: tutor ? tutor.nombre : "No asignado",
      };
    });

    setSearchResults(ninosConTutores);
  };

  const handleToggleVacunas = (ninoId) => {
    setExpandedNinoId(expandedNinoId === ninoId ? null : ninoId);
  };

  const handleEditClick = (nino) => {
    setEditingNino(nino);
    setIsModalOpen(true);
  };

  const handleRegisterClick = () => {
    setEditingNino(null);
    setIsModalOpen(true);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setEditingNino(null);
  };

  const getHistorialVacunas = (ninoId) => {
    if (!dosisAplicadas) return [];
    const dosisDelNino = dosisAplicadas.filter(d => d.id_nio === ninoId);
    return dosisDelNino.map(dosis => {
      const lote = lotesVacunas.find(l => l.id_lote === dosis.id_lote);
      const vacuna = vacunas.find(v => v.id_vacuna === lote?.id_vacuna);
      return {
        ...dosis,
        nombre_vacuna: vacuna?.nombre_vacuna || 'Desconocida',
        numero_lote: lote?.numero_lote || 'N/A'
      };
    });
  };

  const getPlaceholder = () => {
    switch (filterType) {
      case "sector":
        return "Buscar por sector...";
      case "provincia":
        return "Buscar por provincia...";
      case "director":
        return "Buscar por nombre del tutor...";
      case "nombre":
      default:
        return "Buscar por nombre del nio...";
    }
  };

  return (
    <div className="container mt-4">
      <div className="search-container mb-4 d-flex flex-column flex-md-row gap-2 align-items-center">
        <select
          className="form-select"
          value={filterType}
          onChange={(e) => setFilterType(e.target.value)}
          style={{ maxWidth: '200px' }}
        >
          <option value="nombre">Nombre del Nio</option>
          <option value="sector">Sector</option>
          <option value="provincia">Provincia</option>
          <option value="director">Tutor</option>
        </select>
        <input
          type="text"
          className="form-control"
          placeholder={getPlaceholder()}
          value={searchTerm}
          onChange={handleSearch}
        />
        <Button color="success" onClick={handleRegisterClick} className="ms-md-2 mt-2 mt-md-0">
          Registrar Paciente
        </Button>
      </div>

      {searchResults.length > 0 ? (
        <div className="row">
          {searchResults.map((nino) => (
            <div key={nino.id_nio} className="col-12 mb-4">
              <div className="card patient-card">
                <div className="card-body">
                  <h4 className="card-title">{nino.nombre_completo}</h4>
                  <div className="row">
                    <div className="col-md-6">
                      <p><strong>Identificacin:</strong> {nino.identificacion}</p>
                      <p><strong>Fecha de Nacimiento:</strong> {new Date(nino.fecha_nacimiento).toLocaleDateString()}</p>
                      <p><strong>Gnero:</strong> {nino.genero === "M" ? "Masculino" : "Femenino"}</p>
                    </div>
                    <div className="col-md-6">
                      <p><strong>Direccin:</strong> {nino.direccion_residencia}</p>
                      <p><strong>Tutor:</strong> {nino.nombre_tutor}</p>
                    </div>
                  </div>
                  <div className="mt-4 d-flex gap-2">
                    <Button size="sm" color="secondary" onClick={() => handleToggleVacunas(nino.id_nio)}>
                      {expandedNinoId === nino.id_nio ? 'Ocultar Vacunas' : 'Ver Vacunas'}
                    </Button>
                    <Button size="sm" color="primary" onClick={() => handleEditClick(nino)}>
                      Editar
                    </Button>
                  </div>
                  {expandedNinoId === nino.id_nio && (
                    <div className="mt-4 p-3 bg-light rounded">
                      <h5 className="font-weight-bold">Historial de Vacunacin</h5>
                      {getHistorialVacunas(nino.id_nio).length > 0 ? (
                        <ul className="list-unstyled mt-2">
                          {getHistorialVacunas(nino.id_nio).map(vacuna => (
                            <li key={vacuna.id_dosis} className="text-sm mb-2">
                              <strong>{vacuna.nombre_vacuna}</strong> (Dosis {vacuna.dosis_numero})
                              <br />
                              <small className="text-muted">Fecha: {new Date(vacuna.fecha_aplicacion).toLocaleDateString()}</small>
                            </li>
                          ))}
                        </ul>
                      ) : (
                        <p className="text-muted mt-2">No hay vacunas registradas.</p>
                      )}
                    </div>
                  )}
                  <div className="mt-3">
                    <small className="text-muted">
                      ltima actualizacin:{" "}
                      {new Date(nino.fecha_actualizacion).toLocaleDateString()}
                    </small>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : searchTerm.trim() !== "" && (
        <div className="alert alert-info">
          No se encontraron resultados para la bsqueda.
        </div>
      )}

      {isModalOpen && (
        <RegistroForm
          onClose={handleCloseModal}
          ninoToEdit={editingNino}
          onUpdateNino={onUpdateNino}
          onUpdateTutor={onUpdateTutor}
          onNinoAdd={onNinoAdd}
          onTutorAdd={onTutorAdd}
          tutores={tutores}
          centros={centros}
        />
      )}
    </div>
  );
}
