import React, { useState, useEffect } from "react";
import NinoForm from "./NinoForm";
import PadreForm from "./PadreForm";

export default function RegistroForm({ 
  onClose, 
  onNinoAdd, 
  onTutorAdd, 
  ninoToEdit, 
  onUpdateNino, 
  onUpdateTutor, 
  tutores,
  centros
}) {
  const isEditMode = !!ninoToEdit;
  const [paso, setPaso] = useState(1);
  const [formData, setFormData] = useState({
    nombre_completo: "",
    identificacion: "",
    fecha_nacimiento: "",
    genero: "",
    direccion_residencia: "",
    nacionalidad: "",
    pais_nacimiento: "",
    id_centro_salud: "",
    contacto_principal: "",
    id_salud_nacional: "",
  });
  const [padreData, setPadreData] = useState({
    nombre_completo: "",
    identificacion: "",
    relacion: "",
    telefono: "",
    email: "",
    direccion: "",
    nacionalidad: "",
  });
  const [error, setError] = useState("");

  useEffect(() => {
    if (isEditMode && ninoToEdit) {
      const { id_tutor, ...ninoData } = ninoToEdit;
      setFormData(ninoData);
      const tutor = tutores.find(t => t.id_nio === ninoToEdit.id_nio);
      if (tutor) {
        setPadreData({
          nombre_completo: tutor.nombre,
          identificacion: tutor.identificacion,
          relacion: tutor.relacion,
          telefono: tutor.telefono,
          email: tutor.email,
          direccion: tutor.direccion,
          nacionalidad: tutor.nacionalidad,
        });
      }
    }
  }, [isEditMode, ninoToEdit, tutores]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    if (error) setError("");
  };

  const handleChangePadre = (e) => {
    const { name, value } = e.target;
    setPadreData((prev) => ({ ...prev, [name]: value }));
  };

  const siguientePaso = () => {
    const { nombre_completo, fecha_nacimiento } = formData;
    if (!nombre_completo || !fecha_nacimiento) {
      setError("Por favor, complete todos los campos obligatorios del nio.");
      return;
    }
    const hoy = new Date();
    const fechaNac = new Date(fecha_nacimiento);
    let edad = hoy.getFullYear() - fechaNac.getFullYear();
    const mes = hoy.getMonth() - fechaNac.getMonth();
    if (mes < 0 || (mes === 0 && hoy.getDate() < fechaNac.getDate())) {
      edad--;
    }
    if (edad >= 14) {
      setError("El nio debe ser menor de 14 aos.");
      return;
    }
    setError("");
    setPaso(2);
  };

  const volverPaso = () => {
    setPaso(1);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (paso === 1) {
      siguientePaso();
    } else {
      if (!padreData.nombre_completo || !padreData.identificacion || !padreData.relacion || !padreData.telefono) {
        alert("Por favor, completa todos los campos obligatorios del tutor.");
        return;
      }

      if (isEditMode) {
        const updatedNinoData = { ...ninoToEdit, ...formData };
        const originalTutor = tutores.find(t => t.id_nio === ninoToEdit.id_nio);
        if (originalTutor) {
          const updatedTutorData = { ...originalTutor, ...padreData, nombre: padreData.nombre_completo };
          onUpdateTutor(updatedTutorData);
        }
        onUpdateNino(updatedNinoData);
        alert("Informacin actualizada con xito!");
      } else {
        const newNino = { ...formData, id_nio: `temp-${Date.now()}` };
        const newPadre = { ...padreData, id_tutor: `temp-tutor-${Date.now()}`, id_nio: newNino.id_nio, nombre: padreData.nombre_completo };
        onNinoAdd(newNino);
        onTutorAdd(newPadre);
        alert("Nio y tutor registrados con xito!");
      }
      onClose();
    }
  };

  return (
    <div className="modal-overlay">
      <div className={`modal-content card ${isEditMode ? 'editing' : ''}`}>
        <div className="modal-header">
          <h5 className="mb-0">{isEditMode ? 'Editar Informacin del Paciente' : 'Registro de Nio y Tutor'}</h5>
          <button onClick={onClose} className="btn-close" aria-label="Cerrar"></button>
        </div>
        <div className="modal-body">
          {/* Indicador de pasos */}
          <div className="form-step-indicator">
            <div className="step-indicator">
              <div className={`step-circle ${paso === 1 ? 'active' : paso > 1 ? 'completed' : 'inactive'}`}>
                1
              </div>
              <div className={`step-line ${paso > 1 ? 'completed' : ''}`}></div>
              <div className={`step-circle ${paso === 2 ? 'active' : paso > 2 ? 'completed' : 'inactive'}`}>
                2
              </div>
            </div>
          </div>

          <form onSubmit={handleSubmit}>
            {error && (
              <div className="alert alert-danger mb-3" role="alert">
                {error}
              </div>
            )}
            
            <div className="form-content">
              {paso === 1 ? (
                <div className="step-content">
                  <h6 className="text-center mb-4 text-muted">
                    {isEditMode ? 'Informacin del Nio' : 'Paso 1: Informacin del Nio'}
                  </h6>
                  <NinoForm formData={formData} handleChange={handleChange} error={error} centros={centros} />
                </div>
              ) : (
                <div className="step-content">
                  <h6 className="text-center mb-4 text-muted">
                    {isEditMode ? 'Informacin del Tutor' : 'Paso 2: Informacin del Tutor'}
                  </h6>
                  <PadreForm formData={padreData} handleChange={handleChangePadre} />
                </div>
              )}
            </div>
            
            <div className="d-flex justify-content-between align-items-center mt-4 pt-3 border-top">
              {paso === 2 && (
                <button type="button" className="btn btn-secondary" onClick={volverPaso}>
                  <i className="fas fa-arrow-left me-2"></i>
                  Atrs
                </button>
              )}
              <button 
                type="submit" 
                className={`btn ${paso === 1 ? 'btn-primary' : 'btn-success'} ${paso === 1 ? 'ms-auto' : ''}`}
              >
                {paso === 1 ? (
                  <>
                    Siguiente
                    <i className="fas fa-arrow-right ms-2"></i>
                  </>
                ) : (
                  <>
                    <i className="fas fa-save me-2"></i>
                    {isEditMode ? 'Guardar Cambios' : 'Registrar'}
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
