import React, { useState, useRef, useEffect } from "react";
import "./App.css";
import { NextUIProvider } from "@nextui-org/react";
import { Card, CardBody, CardHeader } from "@nextui-org/react";
import { Button } from "@nextui-org/react";
import { Tab, Tabs } from "@nextui-org/react";
import { Modal, ModalBody, ModalHeader } from "@nextui-org/react";
import {
  Table,
  TableHeader,
  TableColumn,
  TableBody,
  TableRow,
  TableCell,
} from "@nextui-org/react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import GestionPacientes from "./components/GestionPacientes";
import RegistroForm from "./components/RegistroForm";
import jsonData from "./json_prueba.json";

// Fix for default marker icon
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

function App() {
  const [activeTab, setActiveTab] = useState("centros");
  const [centrosVacunacion, setCentrosVacunacion] = useState([]);
  const [ninos, setNinos] = useState([]);
  const [tutores, setTutores] = useState([]);
  const [vacunas, setVacunas] = useState([]);
  const [lotesVacunas, setLotesVacunas] = useState([]);
  const [dosisAplicadas, setDosisAplicadas] = useState([]);
  const [showModalCentro, setShowModalCentro] = useState(false);
  const [showPacientesModal, setShowPacientesModal] = useState(false);
  const [selectedCentro, setSelectedCentro] = useState(null);
  const mapRef = useRef(null);

  // State for map filters
  const [filterTerm, setFilterTerm] = useState("");
  const [filterType, setFilterType] = useState("provincia");
  const [filteredCentros, setFilteredCentros] = useState([]);

  useEffect(() => {
    // Cargar datos del JSON
    const centrosGet = jsonData.Centros_Vacunacion.GET;
    const centrosPost = jsonData.Centros_Vacunacion.POST.map((centro) => ({
      ...centro,
      id_centro: `temp-${Math.random()}`,
      fecha_creacion: new Date().toISOString(),
      fecha_actualizacion: new Date().toISOString(),
    }));
    const allCentros = [...centrosGet, ...centrosPost];
    setCentrosVacunacion(allCentros);
    
    const ninosGet = jsonData.Nios.GET;
    const ninosPost = jsonData.Nios.POST.map((nino) => ({
      ...nino,
      id_nio: `temp-${Math.random()}`,
      fecha_creacion: new Date().toISOString(),
      fecha_actualizacion: new Date().toISOString(),
    }));
    setNinos([...ninosGet, ...ninosPost]);
    const tutoresGet = jsonData.Tutores.GET;
    const tutoresPost = jsonData.Tutores.POST.map((tutor) => ({
      ...tutor,
      id_tutor: `temp-${Math.random()}`,
      fecha_creacion: new Date().toISOString(),
      fecha_actualizacion: new Date().toISOString(),
    }));
    setTutores([...tutoresGet, ...tutoresPost]);
    setVacunas(jsonData.Vacunas.GET);
    setLotesVacunas(jsonData.Lotes_Vacunas.GET);
    setDosisAplicadas(jsonData.Dosis_Aplicadas?.GET || []); // Asumiendo que existe esta seccin
  }, []);

  useEffect(() => {
    const term = filterTerm.toLowerCase();
    if (term.trim() === "") {
      setFilteredCentros(centrosVacunacion);
      return;
    }

    const filtered = centrosVacunacion.filter((centro) => {
      const valueToFilter = (() => {
        switch (filterType) {
          case "provincia":
          case "sector":
            return centro.direccion || "";
          case "director":
            return centro.director || "";
          default:
            return "";
        }
      })();
      return valueToFilter.toLowerCase().includes(term);
    });

    setFilteredCentros(filtered);
  }, [filterTerm, filterType, centrosVacunacion]);

  const handleUpdateNino = (updatedNino) => {
    setNinos((prevNinos) =>
      prevNinos.map((nino) =>
        nino.id_nio === updatedNino.id_nio ? updatedNino : nino
      )
    );
  };

  const handleUpdateTutor = (updatedTutor) => {
    setTutores((prevTutores) =>
      prevTutores.map((tutor) =>
        tutor.id_tutor === updatedTutor.id_tutor ? updatedTutor : tutor
      )
    );
  };

  const handleNinoAdd = (newNino) => {
    setNinos((prevNinos) => [...prevNinos, newNino]);
  };

  const handleTutorAdd = (newTutor) => {
    setTutores((prevTutores) => [...prevTutores, newTutor]);
  };


  const handleCentroClick = (centro) => {
    if (mapRef.current) {
      const map = mapRef.current;
      const lat = centro.latitud || 10.4806;
      const lng = centro.longitud || -66.9036;
      map.setView([lat, lng], 15, {
        animate: true,
        duration: 1,
      });
    }
  };

  const handleVerPacientes = (e, centro) => {
    e.stopPropagation();
    setSelectedCentro(centro);
    setShowPacientesModal(true);
  };

  const getPacientesCentro = (idCentro) => {
    return ninos
      .filter((nino) => nino.id_centro_salud === idCentro)
      .map((nino) => ({
        ...nino,
        nombre: nino.nombre_completo.split(" ")[0],
        apellido: nino.nombre_completo.split(" ").slice(1).join(" "),
      }));
  };

  const getTutorNombre = (idTutor) => {
    const tutor = tutores.find((t) => t.id_tutor === idTutor);
    return tutor ? `${tutor.nombre} ${tutor.apellido}` : "No especificado";
  };

  return (
    <NextUIProvider>
      <div className="container-fluid p-4">
        <h1 className="text-center mb-4">Sistema de Vacunacin</h1>

        <Tabs
          selectedKey={activeTab}
          onSelectionChange={setActiveTab}
          aria-label="Opciones"
          className="mb-4"
        >
          <Tab key="centros" title="Centros de Vacunacin" />
          <Tab key="pacientes" title="Pacientes" />
        </Tabs>

        {activeTab === "centros" && (
          <>
            <div className="filter-container mb-4 p-4 border rounded-lg bg-light">
              <div className="d-flex flex-column flex-md-row gap-3">
                <select
                  className="form-select"
                  value={filterType}
                  onChange={(e) => setFilterType(e.target.value)}
                >
                  <option value="provincia">Filtrar por Provincia</option>
                  <option value="sector">Filtrar por Sector</option>
                  <option value="director">Filtrar por Director</option>
                </select>
                <input
                  type="text"
                  className="form-control"
                  placeholder={`Escribe ${filterType} a buscar...`}
                  value={filterTerm}
                  onChange={(e) => setFilterTerm(e.target.value)}
                />
              </div>
            </div>

            <div className="map-container mb-4">
              <MapContainer
                center={[18.4861, -69.9312]} // Coordenadas de Santo Domingo
                zoom={8}
                style={{ height: "400px", width: "100%" }}
                ref={mapRef}
              >
                <TileLayer
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                  attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                />
                {filteredCentros.map((centro) => (
                  <Marker
                    key={centro.id_centro}
                    position={[
                      centro.latitud || 18.4861,
                      centro.longitud || -69.9312,
                    ]}
                  >
                    <Popup>
                      <strong>{centro.nombre_centro}</strong>
                      <br />
                      {centro.direccion}
                      <br />
                      Tel: {centro.telefono}
                    </Popup>
                  </Marker>
                ))}
              </MapContainer>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {filteredCentros.map((centro) => (
                <Card
                  key={centro.id_centro}
                  className="centro-card cursor-pointer"
                  isPressable
                  onPress={() => handleCentroClick(centro)}
                >
                  <CardHeader>
                    <h5 className="text-xl">{centro.nombre_centro}</h5>
                  </CardHeader>
                  <CardBody>
                    <p>
                      <strong>Direccin:</strong> {centro.direccion}
                      <br />
                      <strong>Telfono:</strong> {centro.telefono}
                      <br />
                      <strong>Director:</strong> {centro.director}
                      <br />
                      <strong>Sitio Web:</strong>{" "}
                      <a
                        href={centro.sitio_web}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:text-blue-800"
                        onClick={(e) => e.stopPropagation()}
                      >
                        {centro.sitio_web}
                      </a>
                    </p>
                    <div className="mt-3">
                      <h6 className="font-semibold">
                        Lotes de Vacunas Disponibles:
                      </h6>
                      {lotesVacunas
                        .filter((lote) => lote.id_centro === centro.id_centro)
                        .map((lote) => {
                          const vacuna = vacunas.find(
                            (v) => v.id_vacuna === lote.id_vacuna
                          );
                          return (
                            <div
                              key={lote.id_lote}
                              className="lote-info mt-2 text-sm"
                            >
                              {vacuna?.nombre} - Lote: {lote.numero_lote}
                              <br />
                              Disponibles: {lote.cantidad_disponible} unidades
                              <br />
                              Vence:{" "}
                              {new Date(
                                lote.fecha_vencimiento
                              ).toLocaleDateString()}
                            </div>
                          );
                        })}
                    </div>
                    <Button
                      color="primary"
                      className="mt-4"
                      onClick={(e) => handleVerPacientes(e, centro)}
                    >
                      Ver Pacientes Registrados
                    </Button>
                  </CardBody>
                </Card>
              ))}
            </div>

            <Button
              className="registro-btn fixed bottom-6 right-6"
              onClick={() => setShowModalCentro(true)}
              color="primary"
              size="lg"
              isIconOnly
            >
              +
            </Button>

            {showModalCentro && (
              <RegistroForm
                onClose={() => setShowModalCentro(false)}
                tipo="centro"
              />
            )}

            <Modal
              isOpen={showPacientesModal}
              onClose={() => setShowPacientesModal(false)}
              size="2xl"
            >
              <ModalHeader>
                <h3 className="text-xl font-bold">
                  Pacientes Registrados en {selectedCentro?.nombre_centro}
                </h3>
              </ModalHeader>
              <ModalBody>
                <Table>
                  <TableHeader>
                    <TableColumn>Nombre</TableColumn>
                    <TableColumn>Identificacin</TableColumn>
                    <TableColumn>Fecha de Nacimiento</TableColumn>
                    <TableColumn>Tutor</TableColumn>
                  </TableHeader>
                  <TableBody>
                    {selectedCentro &&
                      getPacientesCentro(selectedCentro.id_centro).map(
                        (paciente) => (
                          <TableRow key={paciente.id_nio}>
                            <TableCell>
                              {paciente.nombre} {paciente.apellido}
                            </TableCell>
                            <TableCell>{paciente.identificacion}</TableCell>
                            <TableCell>
                              {new Date(
                                paciente.fecha_nacimiento
                              ).toLocaleDateString()}
                            </TableCell>
                            <TableCell>
                              {getTutorNombre(paciente.id_tutor)}
                            </TableCell>
                          </TableRow>
                        )
                      )}
                  </TableBody>
                </Table>
              </ModalBody>
            </Modal>
          </>
        )}

        {activeTab === "pacientes" && (
          <GestionPacientes
            ninos={ninos}
            tutores={tutores}
            vacunas={vacunas}
            lotesVacunas={lotesVacunas}
            dosisAplicadas={dosisAplicadas}
            onUpdateNino={handleUpdateNino}
            onUpdateTutor={handleUpdateTutor}
            onNinoAdd={handleNinoAdd}
            onTutorAdd={handleTutorAdd}
            centros={centrosVacunacion}
          />
        )}
      </div>
    </NextUIProvider>
  );
}

export default App;
